.postContainer {
  width: 100%;
  }

.postSectionTop {
width: 100%;
height: 450px;
background-color: var(--body_background);
}

.postHeaderTop {

display: flex;
align-items: center;
}

.postHeaderImage {
  width: 50%;

}
.postHeaderImage img {
  width: 100%;
  height: 450px;
  object-fit: cover;
}

.postHeaderInfo {
  display: flex;
  flex-direction: column;
  align-items: center;
  width: 50%;
  color: var(--body_color);

}

.postHeaderTitle {
  text-align: center;
  margin: 10px;
  font-family: "Lora", serif;
  font-size: 4em;
  color: var(--body_color);
}

.postHeaderHook {
  font-family: "Valera Round", sans-serif;
  font-size: 3em;
  color: var(--body_color);
  margin: 10px;
}

.metaData {
  display: flex;
  width: 100%;
  color: #b39656;
}

.postHeaderAuthor {
  width: 50%;
  padding: 20px;
}

.postHeaderDate {
  width: 50%;
  padding: 20px;
  text-align: right;
}

.postSectionMiddle {
  display: flex;
  height: 150px;
  padding: 20px;
  align-items: center;
}

.socialIcons {
  width: 50%;
  text-align: center;
  color: var(--body_color);
}

.contactMe {
  box-sizing: border-box;
  background-color: black;
  width: 50%;
  text-align: center;
}

.contactMeButton {
  padding: 10px 10px 10px 10px;
  background-color:  #997c3e;
  border-radius: 10px;
}

.contactMeButton .link {
  font-family: "Valera Round", sans-serif;
  font-size: 14px;
  font-weight: 600;
  color: var(--body_color);
  line-height: 1.2em;
  letter-spacing: 0.2px;
}

/* sticky position set to 50px, which the height of the Nav bar */
.postSectionDivider{
  position: sticky;
  top: 50px;
  margin-bottom: 20px;
  }

.hrSolid {
  border: 1px solid var(--body_background);
}

.postSectionBottom {
  display: flex;
  margin-top: 10px;
  margin-bottom: 0px;
  align-items: center;
  justify-content: center;
  color: var(--body_color);
  text-align: center;
}

.postSectionBottom .postBody {
  display: flex;
  width: 1150px;
}

/* sticky position set to 51px, which the height of the Nav bar and the divider section */
.postBodyLeft {
  display: flex;
  flex-direction: column;
  width: 25%;
  height: 600px;
  margin: 10px;
  position: sticky;
  top: 52px;

}

.postBodyRight {
  background-color: green;
  width: 75%;
  margin: 10px;
  padding: 10px;
  text-align: left;
  overflow: hidden;
  object-fit: cover;
  word-wrap: break-word;
}

.image img {
  padding: 10px;
  width: 100%;
  border-radius: 10px;
  object-fit: cover;
}

p img {
  padding: 10px;
  width: 100%;
  border-radius: 10px;
  object-fit: cover;
}




